import { getElementSelector } from '../../utils/dom';
export var ClickListener = function (callback) {
    var recordClick = function (event) {
        if (event.target) {
            var targetSelector = getElementSelector(event.target);
            callback(targetSelector);
        }
    };
    window.addEventListener('click', recordClick);
    return function () { return window.removeEventListener('click', recordClick); };
};
